.paginator {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 10%;
  padding: 20px;
}

.pagination-button {
  max-height: 50%;
  width: 40%;
  margin: auto;
  border: none;
  background: var(--background-light);
  cursor: pointer;
  border-radius: 10px;
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.pagination-button:hover .prev {
  -webkit-animation: move-back 0.5s;
          animation: move-back 0.5s;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

.pagination-button:hover .next {
  -webkit-animation: move-forward 0.5s;
          animation: move-forward 0.5s;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

.pagination-button:disabled:hover .next, .pagination-button:disabled:hover .prev {
  -webkit-animation: none;
          animation: none;
}

.pagination-button:disabled {
  cursor: not-allowed;
}

.pagination-button:active {
  -webkit-box-shadow: 0 2px 2px 2px #6ddd59;
          box-shadow: 0 2px 2px 2px #6ddd59;
}

@-webkit-keyframes move-forward {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  50% {
    -webkit-transform: translateX(40%);
            transform: translateX(40%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes move-forward {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  50% {
    -webkit-transform: translateX(40%);
            transform: translateX(40%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@-webkit-keyframes move-back {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  50% {
    -webkit-transform: translateX(-40%);
            transform: translateX(-40%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes move-back {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  50% {
    -webkit-transform: translateX(-40%);
            transform: translateX(-40%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}


